import React, { Component } from 'react';
import lod from '../img/svg/refresh.svg';
import cross from '../img/svg/cancel.svg';
import fb from '../img/svg/logotype.svg';
import { GoogleLogin } from 'react-google-login';
// import FacebookLogin from 'react-facebook-login';
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';

class LoginReg extends Component {
	responseFacebook = (response) => {
		fetch('/api/register/facebook', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({
				accessToken: response.accessToken,
				pic: response.picture.data.url
			})
		})
			.then((res) => res.json())
			.then((parJson) => {
				console.log(parJson);
				if (parJson.email) {
					window.location.href = '/main';
				} else if (parJson.error) {
					this.setState(() => {
						return {
							loading: false
						};
					});
				}
			})
			.catch((error) => {
				alert(error);
				this.setState(() => {
					return {
						loading: false
					};
				});
			});
	};
	sendLogData() {
		this.setState(() => {
			return {
				loading: true
			};
		});
		let username = document.getElementById('username').value;
		let pass = document.getElementById('pass').value;

		fetch('/api/login', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({
				username: username,
				password: pass
			})
		})
			.then((res) => res.json())
			.then((parJson) => {
				if (parJson.username === username) {
					window.location.href = '/main';
				} else if (parJson.error) {
					alert(parJson.error);

					this.setState(() => {
						return {
							loading: false
						};
					});
				}
			})
			.catch((error) => {
				alert(error);
				this.setState(() => {
					return {
						loading: false
					};
				});
			});
	}

	sendRegData() {
		this.setState(() => {
			return {
				loading: true
			};
		});

		let email = document.getElementById('email').value;
		let pass = document.getElementById('pass').value;
		let name = document.getElementById('name').value;
		let username = document.getElementById('username').value;
		let occupation = document.getElementById('occupation').value;
		let phone_num = document.getElementById('phone_num').value;

		if (email.indexOf('@') === -1 || email.indexOf('.') === -1) {
			alert('Invalid email ');
			return;
		}

		if (pass.length < 8) {
			alert('Password should be greater than 8 letters');
			return;
		}

		fetch('/api/register', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({
				user: {
					username: username,
					email: email,
					password: pass,
					phone_num: phone_num,
					occupation: occupation,
					full_name: name
				}
			})
		})
			.then((res) => res.json())
			.then((parJson) => {
				if (parJson.user.token) {
					this.setlog();
					alert('Registration Complete Now log in ');
					pass = '';

					this.setState(() => {
						return {
							loading: false
						};
					});
				} else if (parJson.error) {
					alert(parJson.error);
					this.setState(() => {
						return {
							loading: false
						};
					});
				}
			})
			.catch((error) => {
				alert(error);
				this.setState(() => {
					return {
						loading: false
					};
				});
			});
	}

	setreg() {
		this.setState(() => {
			return { loc: 'reg' };
		});
	}
	setlog() {
		this.setState(() => {
			return { loc: 'log' };
		});
	}

	responseGoogleError = (response) => {
		console.log(response);
		alert('Unable to login with Google Please try again with different method');
	};

	responseGoogle = (response) => {
		fetch('/api/register/google', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({ tokenId: response.tokenId })
		})
			.then((res) => res.json())
			.then((parJson) => {
				if (parJson.email) {
					window.location.href = '/main';
				} else if (parJson.error) {
					this.setState(() => {
						return {
							loading: false
						};
					});
				}
			})
			.catch((error) => {
				alert(error);
				this.setState(() => {
					return {
						loading: false
					};
				});
			});
	};

	constructor(props) {
		super(props);

		this.setreg = this.setreg.bind(this);
		this.setlog = this.setlog.bind(this);
		this.sendLogData = this.sendLogData.bind(this);
		this.sendRegData = this.sendRegData.bind(this);
		this.state = {
			loc: 'log',
			loading: false
		};
	}

	render() {
		return (
			<div>
				<div className="overlay" onClick={this.props.remLogReg} />

				<div className="logReg">
					<div className="login_head">
						<h1>Accute Accountings</h1>
						<img className="gotohomebtn" onClick={this.props.gotohome} src={cross} alt="" />
					</div>

					<div className="login_body">
						<div className="or_login">Or</div>

						<div className="login_body_left" />

						<div className="login_body_right">
							<GoogleLogin
								clientId="462910295856-qu6jggc7h5mgei80onc32ehhjfiikhsh.apps.googleusercontent.com"
								render={(renderProps) => (
									<button
										type="button"
										onClick={renderProps.onClick}
										disabled={renderProps.disabled}
										className="google_btn socialbtn"
									>
										Continue with Google
									</button>
								)}
								buttonText="Login"
								onSuccess={this.responseGoogle}
								onFailure={this.responseGoogleError}
								cookiePolicy={'single_host_origin'}
							/>
							<FacebookLogin
								appId="259513308534935"
								autoLoad={false}
								fields="name,email,picture"
								render={(renderProps) => (
									<button className="fb_btn socialbtn" onClick={renderProps.onClick}>
										Continue with Facebook
									</button>
								)}
								callback={this.responseFacebook}
							/>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default LoginReg;

